<template>
  <div class="dashboard">
    <!-- 頂部導航 -->
    <header class="header">
      <div class="username">登入帳號：{{ username }}</div>
      <div class="search-bar">
        <img src="@/assets/image 6.png" alt="搜尋圖標" class="search-icon" />
        <input type="text" placeholder="請輸入客戶名稱" v-model="searchQuery" />
      </div>
      <button @click="handleLogout" class="logout-btn">登出</button>
    </header>

    <!-- 分隔線 -->
    <div class="header-divider"></div>

    <!-- 訂單卡片區域 -->
    <div class="orders">
      <div
        v-for="(order, index) in filteredOrders"
        :key="index"
        class="order-card"
      >
        <!-- 上方資訊區 -->
        <div class="order-top">
          <div class="order-info">
            <template v-if="order.urgent">
              <!-- 單一急件條件 -->
              <div class="urgent-wrapper">
                <img src="@/assets/image 5.png" alt="急件" class="urgent-image" />
              </div>
              <span>{{ order.deliveryDate }} 前完成</span>
            </template>
            <span class="bold">下單：{{ order.orderDate }}</span>
            <span class="bold">客戶：{{ order.customer }}</span>
          </div>
          <button class="edit-button" @click="goToEditPage(order.id, order.image)">
            <img src="@/assets/image 4.png" alt="修改圖標" class="edit-icon" /> 資料修改
          </button>
        </div>

        <!-- 加入底部線 -->
        <div class="divider"></div>

        <!-- 顯示已完成區域 -->
        <div v-if="order.completionTime" class="completed-container">
          <div class="completed-circle"></div>
          <div class="completion-time">完成時間：{{ order.completionTime }}</div>
        </div>

        <!-- 中間區域 -->
        <div class="order-middle">
          <!-- 左側圖片 -->
          <div class="image-section">
            <img
              :src="order.image"
              alt="商品圖片"
              class="product-image"
              @click="openModal(order.image)"
            />
          </div>

          <!-- 右側內容 -->
          <div class="details-section">
            <div class="right-column">
              <!-- 內部作業 -->
              <div class="workflow">
                <h3>內部作業</h3>
                <div class="workflow-buttons">
                  <button
                    v-for="(step, index) in order.steps"
                    :key="index"
                    :class="{ active: step.completed }"
                  >
                    {{ step.name }}
                  </button>
                </div>
              </div>

              <!-- 廠商 -->
              <div class="vendors">
                <h3>廠商</h3>
                <div class="vendor-buttons">
                  <button v-if="order.customername1 === 'Y'" class="vendor active">名軒</button>
                  <button v-if="order.customername2 === 'Y'" class="vendor active">鴻昇</button>
                  <button v-if="order.customername3 === 'Y'" class="vendor active">冠達</button>
                </div>
              </div>

              <!-- 貨款與稅金 -->
              <div class="financials">
                <div class="financials-item">
                  <span>貨款金額</span>
                  <div class="amount">{{ order.amount }}</div>
                </div>
                <div class="financials-item">
                  <span>稅金</span>
                  <div class="amount">{{ order.tax }}</div>
                </div>
                <div class="financials-item">
                  <span>尾款</span>
                  <div class="amount">{{ order.balance }}</div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

    <!-- 彈窗 -->
    <div v-if="isModalOpen" class="modal-overlay" @click="closeModal">
      <div class="modal-content" @click.stop>
        <img :src="currentImage" alt="完整圖片" class="modal-image" />
        <button class="close-button" @click="closeModal">關閉</button>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      username: this.$route.query.username || "未知用戶", // 從 Query 獲取參數
      orders: [], // 儲存從 Google Sheets 獲取的訂單資料
      searchQuery: "", // 搜索框輸入值
      isModalOpen: false, // 控制彈窗是否顯示
      currentImage: "", // 當前彈窗中顯示的圖片
    };
  },
  computed: {
    // 根據搜索框的輸入篩選訂單
    filteredOrders() {
      if (!this.searchQuery) return this.orders;
      return this.orders.filter((order) =>
        order.customer.includes(this.searchQuery)
      );
    },
  },
  mounted() {
    this.fetchData(); // 獲取數據
  },
  methods: {
    async fetchData() {
      try {
        const response = await axios.get("https://us-central1-nft-linegame.cloudfunctions.net/function-1/dashboard-data");
        this.orders = response.data.data.map((order) => {
          // 確保圖片處理正確
          const fileIdMatch = order.image.match(/\/d\/(.+?)\//);
          if (fileIdMatch) {
            order.image = `https://drive.google.com/thumbnail?id=${fileIdMatch[1]}&sz=w2000`;
          }
          // 確保 urgent 是布林值
          order.urgent = !!order.urgent;
          return order;
        });
      } catch (error) {
        console.error("獲取 Dashboard 資料失敗", error);
      }
    },
    openModal(image) {
      this.currentImage = image; // 設置當前圖片
      this.isModalOpen = true; // 打開彈窗
    },
    closeModal() {
      this.isModalOpen = false; // 關閉彈窗
      this.currentImage = ""; // 清除當前圖片
    },
    handleLogout() {
      this.$router.push("/"); // 返回登入頁面
    },
    goToEditPage(orderId, imageUrl) {
      this.$router.push({
        name: "EditPage", // 確保這裡的名稱與您的路由定義一致
        params: { id: orderId }, // 傳遞訂單 ID 作為路由參數
        query: { image: imageUrl, username: this.username }, // 傳遞圖片與登入帳號作為 query
      });
    },
  },
};
</script>

<style scoped>
.dashboard {
  font-family: Arial, sans-serif;
  padding: 20px;
}

.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 20px;
  border-bottom: 1px solid #ddd;
}

.header .username {
  font-size: 16px;
  font-weight: bold;
}

.bold {
  color: #243dcb;
}

.search-bar {
  display: flex;
  align-items: center;
  gap: 10px;
  flex-grow: 1;
  margin-left: 1200px;
}

.search-icon {
  width: 20px;
  height: 20px;
  object-fit: contain;
}

.search-bar input {
  padding: 5px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.logout-btn {
  background-color: #add8e6;
  color: black;
  border: none;
  padding: 10px 30px;
  border-radius: 5px;
  cursor: pointer;
}

.header-divider {
  height: 2px;
  background-color: #ccc;
  margin-bottom: 60px;
}

.orders {
  display: flex;
  flex-direction: column;
  gap: 20px;
  align-items: center;
}

.order-card {
  width: 100%;
  max-width: 1500px;
  border: 1px solid Gray;
  border-right: 20px solid red;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  background-color: white;
  padding: 20px;
  display: flex;
  flex-direction: column;
  position: relative; /* 確保內部的灰色區域可以根據位置調整 */
}

.order-top {
  display: flex;  /* 使用 flexbox 來讓所有元素在同一排顯示 */
  justify-content: space-between;  /* 讓元素兩端對齊 */
  align-items: center;  /* 保證所有元素在垂直方向上居中對齊 */
  margin-bottom: 10px;
  gap: 100px;  /* 控制元素之間的間距 */
  margin-top: -50px; /* 向上移動 */
}

.order-info {
  display: flex;
  flex-wrap: nowrap;  /* 防止換行 */
  justify-content: flex-start;  /* 使元素從左至右排列 */
  font-size: 20px;
  font-weight: bold;
  gap: 150px;  /* 每個元素之間的間距 */
  line-height: 10;  /* 調整行高，使文字垂直居中或稍微向下 */
}

.order-info span {
  display: inline-block; /* 確保每個項目都處於一行 */
}

.divider {
  border-bottom: 2px solid black;
  margin-top: -60px; /* 向上移動 */
  margin-bottom: 20px; /* 可以保持原來的底部距離 */
}

.urgent {
  background-color: red;
  color: white;
  padding: 5px 10px;
  border-radius: 4px;
  font-weight: bold;
}

.non-urgent {
  color: gray;
  font-weight: bold;
  font-size: 16px;
}

.order-middle {
  display: flex;
  justify-content: flex-start;
  gap: 20px;
}

.image-section {
  flex: 2;
  display: flex;
  align-items: flex-end;
}

.product-image {
  width: 100%;
  height: auto;
  object-fit: contain;
  border-radius: 8px;
  cursor: pointer;
}

.details-section {
  flex: 3;
  display: flex;
  flex-direction: column;
  gap: 15px;
  justify-content: space-between;
}

.workflow-buttons,
.vendor-buttons {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  justify-content: flex-start;
}

.workflow-buttons button,
.vendor {
  width: 70px;
  height: 70px;
  font-size: 20px;
  font-weight: bold;
  background-color: gray;
  color: white;
  border: none;
  border-radius: 8px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.workflow-buttons button.active,
.vendor.active {
  background-color: red;
}

.financials {
  display: flex;
  justify-content: space-between;
  gap: 10px;
  margin-top: 60px;
}

.financials-item {
  display: flex;
  flex-direction: column;  /* 垂直排列項目和數值 */
  justify-content: flex-start;
  width: 30%;
  font-size: 18px;
  text-align: center;  /* 使文字居中 */
}

.financials-item span {
  display: block;
  font-weight: bold;
  margin-bottom: 5px;  /* 添加間距，讓黑線與文字之間有空隙 */
}

.amount {
  border-top: 4px solid black;  /* 黑線放在數值上方 */
  padding-top: 3px;  /* 給數值與黑線之間添加間距 */
  width: 80%;  /* 設定黑線的長度，可以根據需要調整百分比 */
  margin: 0 auto;  /* 使黑線居中顯示 */
}

.edit-button {
  background-color: #a1e1a1;
  border: none;
  padding: 10px 30px;
  border-radius: 4px;
  font-weight: bold;
  cursor: pointer;
  display: flex;
  align-items: center;
  font-size: 18px;
  font-weight: bold;
}

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.modal-content {
  position: relative;
  background-color: white;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.modal-image {
  width: 100%;
  max-width: 90vw;
  height: auto;
  max-height: 80vh;
  border-radius: 8px;
  display: block;
  margin: 0 auto;
}

.close-button {
  position: absolute;
  top: 10px;
  right: 10px;
  background-color: red;
  color: white;
  border: none;
  padding: 5px 10px;
  border-radius: 5px;
  cursor: pointer;
}

.urgent-image {
  width: 40px;
  height: auto;
  margin-right: 10px;
  vertical-align: middle;
}

.urgent-image {
  width: 80%;
  height: 80%;
  object-fit: contain;
  vertical-align: middle;
}

.edit-icon {
  width: 16px; /* 設定圖片寬度 */
  height: 16px; /* 設定圖片高度 */
  margin-right: 8px; /* 調整圖片和文字之間的間距 */
}

.completed-container {
  background-color: rgba(0, 0, 0, 0.5); /* 淡黑色背景並增加透明度 */
  padding: 20px;
  border-radius: 8px;
  position: absolute; /* 絕對定位 */
  top: calc(18% + 20px); /* 從divider下方開始，並保留一點空隙 */
  left: 0;
  right: 0;
  bottom: 0; /* 確保灰色區域延伸到order-card的底部 */
  z-index: 1; /* 確保灰色區域在所有元素之上 */
}

.completed-circle {
  position: relative;
  display: inline-block;
  background-image: url('@/assets/image 3.png'); /* 輸入您的圖片路徑 */
  background-size: cover; /* 使圖片覆蓋圓圈 */
  color: transparent; /* 讓文字不顯示 */
  padding: 30px 50px; /* 調整圓圈的大小 */
  border-radius: 50%; /* 保持圓形 */
  margin-bottom: 20px;
  width: 200px; /* 圓圈的寬度 */
  height: 200px; /* 圓圈的高度 */
  right: -600px;
  text-align: center;
}

.completion-time {
  font-size: 16px; /* 完成時間文字大小 */
  font-weight: bold;
  color: white; /* 文字顏色為白色 */
  text-align: center; /* 文字居中 */
  margin-top: -10px; /* 增加與圓圈的間距 */
  padding: 10px; /* 添加填充，增強排版效果 */
  background-color: rgba(0, 0, 0, 0.7); /* 背景色透明 */
  border-radius: 5px; /* 邊框圓角 */
}

</style>
